
footer{
  background: rgb(255, 255, 255);
}

.contact_us {
  position: relative;
  height: 867px;
  background: rgb(44, 44, 44);
  border-radius: 50px 50px 0 0;
}

.footer_block {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  display: flex;
  flex-direction: column;
}

.footer_items {
  display: flex;
  justify-content: center;
align-items: center;
  padding-top: 759px;
  gap: 463px;

  @media (max-width: 1024px) {
    gap: 233px;
  }
}

.footer_nav {
  flex-grow: 1;
  display: flex;
  gap: 48px;
}

.footer_link {
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 3px;
  text-align: left;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: blue;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.3s ease;
  }

  &:hover::after {
    transform: scaleX(1);
  }
}
