.header_image {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 390px;
  bottom: 0;
  display: grid;
  grid-template-columns: 100%;
  border-radius: 30px;
  transition: border-radius 0.3s;

  @include on-desktop {
    width: 50%;
    height: 680px;
    top: 120px;
    right: 0;
    border-radius: 30px 0;
  }
}

.slide {
  width: 100%;
  height: 390px;

  @include on-desktop {
    height: 680px;
  }
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.text-overlay {
  position: absolute;
  z-index: 2;
  margin-left: 650px;
  bottom: 24px;
}

.overlay-title {
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 3px;
  text-align: left;
  text-transform: uppercase;
}

.overlay-text {
  width: 264px;
  color: rgb(255, 255, 255);
  font-family: OpenSans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: 0;
  text-align: left;
}

.intro_buttons {
  position: absolute;
  bottom: 66px;
  margin-left: 200px;
  z-index: 2;
}

.arrow_left {
  cursor: pointer;
  background: rgb(255, 255, 255);
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 50%;
  &:hover {
    transform: scale(1.1);
    transition:
      background-color 0.3s,
      border-radius 0.3s,
      transform 0.3s;
  }
}

.arrow_right {
  cursor: pointer;
  margin-left: 16px;
  background: rgb(255, 255, 255);
  width: 32px;
  height: 32px;
  border: none;
  border-radius: 50%;
  &:hover {
    transform: scale(1.1);
    transition:
      background-color 0.3s,
      border-radius 0.3s,
      transform 0.3s;
  }
}

.slider-inner {
  display: grid;
  grid-template-columns: repeat(4, 100%);
  transition: transform 500ms ease-in-out;
}

