.apply {
  position: relative;
  height: 691px;
  width: 100%;
  background: rgb(255, 255, 255);
  margin-top: 148px;
  display: flex;
  justify-content: center;
}

.apply_shapes {
  margin-top: 72px;
  position: relative;
  width: 1450.8px;
  height: 546.86px;
  background-image: url(../images/Shapes.png);
  display: flex;
  justify-content: center;
  align-items: center;
}

.apply_text_block {
  position: absolute;
  width: 754px;
  height: 294px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.apply_title {
  margin-top: 0;
  color: rgb(37, 55, 87);
  font-family: Poppins, sans-serif;
  font-size: 52px;
  font-weight: 600;
  line-height: 150%;
  text-align: center;
}

.apply_text {
  color: rgb(108, 120, 139);
  font-family: OpenSans, sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 150%;
  text-align: center;
}

.apply_button {
  cursor: pointer;
  width: 264px;
  height: 50px;
  border-radius: 8px;
  border: none;
  background-color: #2060f6;
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 23px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.1);
    transition:
      background-color 0.3s,
      border-radius 0.3s,
      transform 0.3s;
  }
}
