.aside-menu {
  box-sizing: border-box;
  height: 100vh;

  background-color: rgb(44, 44, 44);

  opacity: 0;
  transform: translateX(-100%);
  transition:
    opacity 0.3s,
    transform 0.3s;
  pointer-events: none;
  z-index: 10;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.menu__top {
  padding: 20px;
}

.menu__link {
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 3px;
  text-align: left;
  text-transform: uppercase;
  text-decoration: none;
}

.aside-menu:target {
  opacity: 1;
  transform: translateX(0);
  pointer-events: all;
}

.nav_menu__nav {
  top: 25%;
  left: 50%;
  transform: translate(-50%, 0);
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 45px;
}

.icon--close {
  margin-left: 60vw;

  @include on-tablet {
    margin-left: 80vw;
  }
}
