.conteiner {
  background: rgb(44, 44, 44);
  height: 800px;
  border-radius: 0 0 50px 50px;
  position: relative;
  display: flex;
  justify-content: center;
}

.top_toper {
  display: flex;
  flex-direction: column;
  z-index: 2;
}

.header {
  display: flex;
  justify-content: center;
  padding-top: 49px;
  flex-direction: row;
  align-items: center;
  gap: 251px;

  @media (max-width: 1024px) {
  gap: 136px;
  }

}

.header__nav {
  display: flex;
  align-items: center;
  gap: 48px;
}


.header__link {
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: blue;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.3s ease;
  }

  &:hover::after {
    transform: scaleX(1);
  }
}

.hire_us {
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;




  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: blue;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.3s ease;
  }

  &:hover::after {
    transform: scaleX(1);
  }
}

.text__block {
  width: 490px;
  height: 355px;
  margin-top: 165px;
}

.title {
  margin-top: 0;
  max-width: 400px;
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 64px;
  font-weight: 600;
  line-height: 68px;
  text-align: left;
}

.text {
  margin-top: 32px;
  width: 362px;
  color: rgb(255, 255, 255);
  font-family: OpenSans, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
  text-align: left;
}

.header__button {
  margin-top: 43px;
  cursor: pointer;
  width: 264px;
  height: 50px;
  border-radius: 8px;
  background-color: #2060f6;
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 23px;
  text-align: center;
  transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.1);
    transition:
      background-color 0.3s,
      border-radius 0.3s,
      transform 0.3s;
  }
}

.header_image {
  position: absolute;
  margin-left: 196px;
}

@media (min-width: 1024px) {
  .menu {
    display: none;
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 34px;
}

.slider::before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider::before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round::before {
  border-radius: 50%;
}
