.services {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin-top: 148px;
  width: 1148px;
  height: 690px;
}

@media (max-width: 1024px) {
  .services {
    width: 918px;
    height: 687px;
  }
}

.text_conteiner {
  position: absolute;
  margin-top: 103px;
  width: 364px;
  height: 249px;
}

.services_subtitle {
  margin-top: 0;
