}

.service_cards {
  position: absolute;
  display: flex;
  flex-direction: row;
  gap: 30px;
}

@media (max-width: 1024px) {
  .service_cards {
    gap: 20px;
  }
}

.service_cards_grup_1 {
  margin-top: 132px;
