}

.service_card {
  cursor: pointer;
  width: 264px;
  height: 264px;
  border-radius: 16px;
  box-shadow: 0 9px 18px 0 rgba(37, 41, 49, 0.03);
  background: rgb(255, 255, 255);
    transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.09);
  }
}

@media (max-width: 1024px) {
  .service_card {
    width: 214px;
  }
}

.service_card_text {
  width: 200px;
  height: 80px;
  margin-top: 80px;
