.main_container {
  margin-top: 180px;
  position: absolute;
  display: flex;
  flex-direction: row;
  gap: 226px;
}

@media (max-width: 1024px) {
  .main_container {
    gap: 98px;
  }
}

.contacts {
  width: 362px;
  height: 415px;
  position: relative;
}

.contacts_title {
  margin-top: 0;
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 32px;
  font-weight: 600;
  line-height: 150%;
  text-align: left;
}

.contact_subtitle {
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 3px;
  text-align: left;
  text-transform: uppercase;
  opacity: 0.5;
}

.contac_info {
  font-family: Poppins, sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 150%;
  color: #fff;
  text-decoration: none;
  display: block;
  transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.05);
  }
}

.call_us {
  margin-top: 46px;
}

.visit_us {
  margin-top: 38px;
}

.social_media {
  margin-top: 38px;
}

.contact_social-media {
  position: absolute;
  display: flex;
  flex-direction: row;
  gap: 32px;
}

.social-media__icon_instagram {
  filter: none;
  transition: filter 0s ease;
}

.social-media__icon_instagram:hover {
  filter: invert(1233%) sepia(100%) saturate(54560%) hue-rotate(210deg)
    brightness(110%) contrast(117%);
}

.social-media__icon_facebook {
  filter: none;
  transition: filter 0s ease;
}

.social-media__icon_facebook:hover {
  filter: invert(1233%) sepia(100%) saturate(54560%) hue-rotate(210deg)
    brightness(110%) contrast(117%);
}

.social-media__icon_twitter {
  filter: none;
  transition: filter 0s ease;
}

.social-media__icon_twitter:hover {
  filter: invert(1233%) sepia(100%) saturate(54560%) hue-rotate(210deg)
    brightness(110%) contrast(117%);
}
