.message {
  position: relative;
  width: 460px;
  height: 475px;
}

@media (max-width: 1024px) {
  .message {
    width: 370px;
  }
}

.message_title {
  margin-top: 0;
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 32px;
  font-weight: 600;
  line-height: 150%;
  text-align: left;
}

input {
  border: none;
  background: none;
  border-bottom: 1px solid #fff;
  width: 100%;
  display: flex;
  padding-bottom: 16px;
  margin-bottom: 40px;
  font-family: Poppins, sans-serif;
  font-weight: 600;
  font-size: 15px;
  line-height: 100%;
  color: #fff;
  &::placeholder{
    color: rgb(255, 255, 255);
    opacity: 0.7;
   }
  &:hover {
    border-bottom: 1px solid #144ed4;
  }

  &:focus,
  &:active {
    outline: none;
  }

  &:-webkit-autofill {
    transition: all 5000s ease-in-out 0s;
  }
}

textarea {
  border: none;
  background: none;
  border-bottom: 1px solid #fff;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 56px;
  resize: none;
  color: #fff;
  font-family: Poppins, sans-serif;
  font-weight: 600;
  font-size: 15px;
  line-height: 100%;
  &::placeholder{
    color: rgb(255, 255, 255);
    opacity: 0.7;
   }
  &:hover {
    border-bottom: 1px solid #144ed4;
  }

  &:focus,
  &:active {
    outline: none;
  }
}

.message_button {
  margin-top: 60px;
  cursor: pointer;
  width: 264px;
  height: 50px;
  border-radius: 8px;
  background-color: #2060f6;
  color: rgb(255, 255, 255);
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 23px;
  text-align: center;
    transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.1);
    transition:
      background-color 0.3s,
      border-radius 0.3s,
      transform 0.3s;
  }
}

.message_info {
  margin-top: 56px;
}
