.about_us {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about_us_title {
  position: absolute;
  align-items: center;
  color: rgb(37, 55, 87);
  font-family: Poppins, sans-serif;
  font-size: 32px;
  font-weight: 600;
  line-height: 150%;
  text-align: center;
}

.about_us_text {
  padding-top: 212px;
  width: 754px;
  color: rgb(108, 120, 139);
  font-family: OpenSans, sans-serif;
  font-size: 24px;
  font-weight: 600;
  line-height: 150%;
  text-align: center;
}
