.expertise {
  border-radius: 30px;
  background: rgb(255, 255, 255);
  position: relative;
  height: 759px;
  margin-top: 148px;
}

.esp_tittle {
  padding-top: 128px;
  color: rgb(37, 55, 87);
  font-family: Poppins, sans-serif;
  font-size: 52px;
  font-weight: 600;
  line-height: 150%;
  text-align: center;
}

.card_tittle {
  margin-top: 206px;
  position: absolute;
  color: rgb(37, 55, 87);
  font-family: Poppins, sans-serif;
  font-size: 20px;
  font-weight: 600;
  line-height: 140%;
  text-align: center;
}

.card_text {
  margin-top: 250px;
  position: absolute;
  width: 264px;
  color: rgb(108, 120, 139);
  font-family: OpenSans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
  text-align: center;

  @media (min-width: 1024px) {
    width: 214px;
  }
}

.expertise_cards {
  margin-top: 64px;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  gap: 177px;

  @media (min-width: 1024px) {
    gap: 137px;
  }
}

.expertise--card {
  display: flex;
  justify-content: center;
  width: 264px;
  height: 334px;

  @media (min-width: 1024px) {
    width: 214px;
  }
}

.mask {
  position: absolute;

  overflow: hidden;
}
