.testomonials {
  width: 1146px;
  height: 711px;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin-top: 148px;
}

@media (max-width: 1024px) {
  .testomonials {
    width: 916px;
  }
}

.testomonials_subtitle {
  color: rgb(192, 205, 215);
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  letter-spacing: 3px;
  text-align: left;
  text-transform: uppercase;
}

.testomonials_tittle {
  margin-top: 16px;
  color: rgb(37, 55, 87);
  font-family: Poppins, sans-serif;
  font-size: 32px;
  font-weight: 600;
  line-height: 150%;
  text-align: left;
}

.testomonials_card {
  cursor: pointer;
  position: relative;
  width: 362px;
  height: 494px;
  border-radius: 16px;
  box-shadow: 0 9px 18px 0 rgba(37, 41, 49, 0.03);
  background: rgb(255, 255, 255);
    transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.05);
  }
}

@media (max-width: 1024px) {
  .testomonials_card {
    width: 292px;
    height: 456px;
  }
}

